import React from "react";
import { Box, Button, Heading, Text } from "@chakra-ui/react";
import { useAuth0 } from "@auth0/auth0-react";

const Home = () => {
  const { loginWithRedirect } = useAuth0();
  const { logout } = useAuth0();
  const { user, isAuthenticated, isLoading } = useAuth0();

  return (
     <>
        <Box
      display="flex"
      flexDirection="column"
      justifyContent="center"
      alignItems="center"
      height="100vh"
    >
      <Box maxW="container.md" py={12} px={6} textAlign="center">
        <Heading as="h1" size="2xl" mb={4}>
          Welcome to my website
        </Heading>
        <Text fontSize="lg" mb={8}>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nullam
          suscipit, nibh at egestas tristique, dolor enim euismod turpis, vel
          accumsan nunc enim eu magna. Donec malesuada justo velit, at
          tristique ex bibendum quis.
        </Text>
        {
          isAuthenticated ? (
            <Button colorScheme="blue" size="lg" onClick={() => logout({ logoutParams: { returnTo: window.location.origin } })}>
          Log Out
        </Button>
          ) :(
            <Button colorScheme="blue" size="lg"  onClick={() => loginWithRedirect()} >
          Get started/Log IN
        </Button>
          )
        }
         
      </Box>
    </Box>
     </>
  );
};

export default Home;
