import React from "react";
import { Box } from "@chakra-ui/react";


const About = () => {
  return (
    <Box
      display="flex"
      flexDirection="column"
      justifyContent="center"
      alignItems="center"
      height="100vh"
    >
      <h1 style={{fontSize:"50px",padding:"20%"}}>
        About
      </h1>
    </Box>
  );
};

export default About;
