import React from "react";
import { Box } from "@chakra-ui/react";

const Projects = () => {
  return (
    <>
       <Box
      display="flex"
      flexDirection="column"
      justifyContent="center"
      alignItems="center"
      height="100vh"
    >
      <h1 style={{fontSize:"50px",padding:"20%"}}>
        Project
      </h1>
    </Box>
    </>
  );
};

export default Projects;
